.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}
/* Styles for a responsive login form suitable for small iframes */

body {
  margin: 0;
  font-family: Arial, sans-serif;
  background: linear-gradient(135deg, #4e2a8e, #7a52cc);
  height: 100%;
  display: flex;
  justify-content: center;
  align-items: center;
  color: white;
}

.login-container {
  background: rgba(255, 255, 255, 0.1);
  padding: 20px;
  border-radius: 10px;
  box-shadow: 0px 0px 10px rgba(0, 0, 0, 0.5);
  text-align: center;
  width: 100%;
  max-width: 350px; /* Smaller max-width for iframes */
  margin: 10px; /* Adjusts within small iframes */
  display: flex;
  flex-direction: column;
  justify-content: center;
}

h1 {
  font-size: 20px; /* Smaller font size */
  margin-bottom: 15px;
}

.input-container {
  margin-bottom: 15px;
  text-align: left;
}

label {
  display: block;
  margin-bottom: 5px;
  font-weight: bold;
}

input[type="text"],
textarea,
select {
  width: 100%;
  padding: 8px; /* Reduced padding for smaller size */
  border: none;
  border-radius: 5px;
  background-color: rgba(255, 255, 255, 0.2);
  color: white;
  font-size: 14px;
  margin-bottom: 10px;
  outline: none;
}

input[type="text"]::placeholder,
textarea::placeholder {
  color: rgba(255, 255, 255, 0.7);
}

textarea {
  resize: none;
  height: 60px;
}

button[type="submit"] {
  width: 100%;
  padding: 10px; /* Reduced padding for button */
  border: none;
  border-radius: 5px;
  background-color: #5a4cad;
  color: white;
  font-size: 16px;
  cursor: pointer;
  margin-top: 10px;
}

button[type="submit"]:hover {
  background-color: #473b8d;
}

.error-message {
  color: #ff4d4d;
}

.success-message {
  color: #4caf50;
}
